import React from 'react'

const Navbar = () => {
  return (
    <nav className="forDesktop">
      <h3>Mens' Fashion</h3>


      <div class="socialIcons">
                        <i class="fab fa-linkedin-in" />
                        <i class="fab fa-twitter" />
                        <i class="fab fa-facebook" />
                        <i class="fab fa-google-plus" />
                    </div>
    </nav>
  )
}

export default Navbar;


